<template>
    <div class="refresh" ref="refresh">
        <div class="refreshBox" ref="refreshBox">
            <img src="../assets/img/downRefresh.svg" alt="" />
        </div>
    </div>
</template>
 
<script>
export default {
    mounted() {
        this.touchStartBox();
        this.touchMoveBox();
        this.touchEndBox();
    },
    data() {
        return {
            startY: "", //保存touch时的Y坐标
            startX: "", //保存touch时的X坐标
            moveDistance: 0, //保存向下滑动的距离
            moveState: 0, //开始滑动到结束后状态的变化 0:下拉即可刷新 1:释放即可刷新 2:加载中
            duration: 0 //动画持续时间，0就是没有动画
        };
    },
    methods: {
        // 按下
        touchStartBox() {
            this.$parent.$el.addEventListener("touchstart", e => {
                //初始化
                this.moveState = 0;
                this.moveDistance = 0;
                //去除动画
                this.startY = e.targetTouches[0].clientY;
                this.$refs.refresh.style.transitionDelay = "0";
                this.$refs.refreshBox.style.transform = `rotate(0deg)`;
            });
        },
        //滑动
        touchMoveBox() {
            let scrollTop =
                document.documentElement.scrollTop || document.body.scrollTop;
            //首先判断我们有没有滚动条，如果有，我们下拉刷新就不能启用。
            if (scrollTop > 0) return;
            let move = "";
            this.$parent.$el.addEventListener("touchmove", e => {
                move = e.targetTouches[0].clientY - this.startY;
                if (move > 0) {
                    e.preventDefault();
                    //增加滑动阻力的感觉
                    this.moveDistance = Math.pow(move, 0.8);
                    this.$refs.refresh.style.transform = `translateY(${
                        this.moveDistance - 30
                    }px)`;
                    if (this.moveDistance > 60) {
                        if (this.moveState === 1) return;
                        this.moveState = 1;
                        this.moveDistance = 60;
                    } else {
                        if (this.moveState === 0) return;
                        this.moveState = 0;
                        this.moveDistance = 0;
                    }
                }
            });
        },
        //离开
        touchEndBox() {
            this.$parent.$el.addEventListener("touchend", e => {
                if (this.moveDistance >= 60) {
                    this.moveState = 2;
                    this.moveDistance = 60;
                    this.$emit("refresh", () => {
                        this.moveState = 0;
                        this.moveDistance = -30;
                    });
                } else {
                    this.moveState = 0;
                    this.moveDistance = -30;
                    this.$refs.refresh.style.transform = `translateY(${this.moveDistance}px)`;
                }
            });
        }
    },
    watch: {
        //这里是给用户操作返回的核心
        moveState(state) {
            if (this.moveState === 2) {
                this.$refs.refresh.style.transform = `translateY(${this.moveDistance}px)`;
                this.$refs.refreshBox.style.transform = `rotate(360deg)`;
            } else {
                this.$refs.refresh.style.transform = `translateY(${this.moveDistance}px)`;
            }
        }
    }
};
</script>
<style scoped lang="less">
.refresh {
    width: 100%;
    position: fixed;
    z-index: 1000;
    transform: translateY(-30px);
    .refreshBox {
        margin: 0 auto;
        width: 26px;
        height: 26px;
        border-radius: 50%;
        background-color: #fff;
        box-shadow: 0 0 5px 2px #999;
        text-align: center;
        transform: rotate(0);
        transition: all 0.5s linear;
        opacity: 1;
        img {
            width: 80%;
            margin-top: 10%;
        }
    }
}
</style>