<template>
    <div class="tuijian" ref="tuijianBox">
        <down-refresh @refresh="refresh"></down-refresh>
        <div class="top">
            <van-search
                v-model="searchvalue"
                show-action
                shape="round"
                placeholder="请输入搜索关键词"
                input-align="center"
                left-icon
                clearable
            >
                <template #action>
                    <div class="personal" v-if="personalimg">
                        <img :src="personalimg" alt />
                    </div>
                    <div class="nologin" v-else @click="goLogin">
                        <img src="../../assets/img/nologin.svg" alt />
                    </div>
                </template>
                <template #left>
                    <div class="mai"></div>
                </template>
                <template #right-icon>
                    <van-icon
                        name="search"
                        size="20"
                        is-link
                        @click="getSearch"
                    />
                </template>
            </van-search>

            <van-popup
                class="popupBox"
                v-model="show"
                round
                position="top"
                :style="{ height: '30%' }"
            >
                <div class="searchPopupList">
                    <div
                        class="ListItem flexbetween"
                        v-for="(item, index) in searchData"
                        :key="index"
                    >
                        <div class="listItem_left">
                            <span class="name">{{ item.name }}</span>
                            <span class="heng">-</span>
                            <span class="singer">{{ item.artNameData }}</span>
                        </div>
                        <div class="listItem_right" @click="clicksetUrl(index)">
                            <img src="../../assets/img/play.svg" alt />
                        </div>
                    </div>
                </div>
            </van-popup>
        </div>
        <div class="body">
            <!-- 轮播图 -->
            <div class="swipebox">
                <van-swipe
                    class="my-swipe"
                    indicator-color="white"
                    :autoplay="3000"
                >
                    <van-swipe-item
                        v-for="(item, index) in swipeData"
                        :key="index"
                    >
                        <img :src="item" alt />
                    </van-swipe-item>
                </van-swipe>
            </div>
            <div class="inletbox">
                <div
                    class="inletitem"
                    v-for="(item, index) in inletboxData"
                    :key="index"
                >
                    <img :src="item.img" alt />
                    <p>{{ item.title }}</p>
                </div>
            </div>
            <div class="tuijiansonglist">
                <div class="songlistTop flexbetween">
                    <div>
                        <p>推荐歌单</p>
                    </div>
                    <div class="flexbetween">
                        <span>歌单广场</span>
                        <van-icon name="arrow" size="14" />
                    </div>
                </div>
                <div class="songlistBody">
                    <van-tabs v-model="active">
                        <van-tab
                            v-for="(item, index) in songlistData"
                            :key="index"
                        >
                            <template #title>
                                <div class="dian"></div>
                            </template>
                            <div
                                class="songshowbox"
                                @touchstart="touchstartShowBox($event, index)"
                                @touchmove="touchmoveShowBox($event, index)"
                                @touchend="touchendShowBox($event, index)"
                            >
                                <div class="songbox">
                                    <div
                                        class="songitem"
                                        v-for="(v, i) in item"
                                        :key="i"
                                        @click="clicksongitem(v.id)"
                                    >
                                        <img :src="v.picUrl" alt />
                                        <div class="namebox">{{ v.name }}</div>
                                        <div class="iconbox">
                                            <img
                                                src="../../assets/img/tj_a.png"
                                                alt
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </van-tab>
                    </van-tabs>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import "../../assets/less/tuijian.less";
import { setMusicData } from "../../assets/js/setMusicData";
import { createNamespacedHelpers } from "vuex";
import { setupCookie } from "../../assets/js/setupCookie";
import { Form } from "vant";
import DownRefresh from "../../components/Downrefresh";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    name: "tuijian",
    components: {
        DownRefresh
    },
    created() {
        this.getswipeData();
        this.getsonglist();
        // 获取用户登录状态
        this.getuserStatus();
        // //设置音乐组件位置
        if (this.clickSong) {
            this.setaudioxy(0);
        }
    },

    computed: {
        ...musicState(["detailbool", "clickSong"])
    },
    watch: {
        clickSong(val) {
            if (val) {
                this.setaudioxy(0);
            }
        }
    },
    beforeRouteEnter: (to, from, next) => {
        console.log("to :>> ", to);
        console.log("from :>> ", from);
        next();
    },
    data() {
        return {
            show: false,
            searchvalue: "",
            //搜索到的数据
            searchData: [],
            //根据id获取的歌曲数据
            songData: [],
            swipeData: [],
            //头像
            personalimg: "",
            // 入口数据
            inletboxData: {
                everytujian: {
                    title: "每日推荐",
                    url: {
                        name: "Everytujian"
                    },
                    img: require("..//../assets/img/1.png")
                },
                songlist: {
                    title: "歌单",
                    url: {
                        name: "Songlist"
                    },
                    img: require("..//../assets/img/2.png")
                },
                ranking: {
                    title: "排行榜",
                    url: {
                        name: "Ranking"
                    },
                    img: require("..//../assets/img/3.png")
                },
                radiostation: {
                    title: "电台",
                    url: {
                        name: "Radiostation"
                    },
                    img: require("..//../assets/img/4.png")
                }
            },
            //导航标记
            active: 0,
            //歌单数据
            songlistData: []
        };
    },
    methods: {
        ...musicActions([
            "setaudioxy",
            "setsongData",
            "seticonNum",
            "setClickSong"
        ]),
        //刷新
        refresh(done) {
            setTimeout(() => {
                this.getswipeData();
                this.getsonglist();
                done();
            }, 1000);
        },

        // 获取轮播图
        getswipeData() {
            this.axios({
                method: "GET",
                url: "/banner",
                params: {
                    type: 2
                }
            })
                .then(result => {
                    this.swipeData = [];
                    result.data.banners.slice(4, 10).map(v => {
                        this.swipeData.push(v.pic);
                    });
                })
                .catch(err => {});
        },

        //获取歌单
        getsonglist() {
            //获取cookie
            let cookie = setupCookie.getCookies();

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/personalized",
                params: {
                    limit: 20,
                    cookie
                }
            })
                .then(result => {
                    this.$toast.clear();
                    let recommendData = result.data.result;
                    this.songlistData = [];
                    for (let i = 0; i < 4; i++) {
                        this.songlistData.push(
                            recommendData.slice(i * 5, i * 5 + 5)
                        );
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取用户登录状态
        getuserStatus() {
            //获取cookie
            let cookie = setupCookie.getCookies();
            if (cookie.split(";")[0] == "null") {
                this.personalimg = null;
                return;
            }
            this.axios({
                method: "GET",
                url: "/login/status",
                params: {
                    cookie
                }
            })
                .then(result => {
                    this.personalimg = result.data.profile.avatarUrl;
                    this.$toast({
                        message: result.data.profile.nickname,
                        icon: require("../../assets/img/tj_a.png")
                    });
                })
                .catch(err => {});
        },

        //跳转到登录
        goLogin() {
            this.$router.push({ name: "Login" });
        },

        //点击歌单跳转
        clicksongitem(id) {
            this.$router.push({
                name: "Listdetails",
                query: { id }
            });
        },

        //搜索
        getSearch() {
            if (this.searchvalue == "") {
                return;
            }
            this.show = true;
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "GET",
                url: "/search",
                params: {
                    limit: 20,
                    keywords: this.searchvalue
                }
            })
                .then(result => {
                    this.$toast.clear();
                    if ((result.status = "200")) {
                        this.searchData = [];
                        let data = "";
                        data = result.data.result.songs;
                        let artNameData = "";
                        data.map(v => {
                            for (let i = 0; i < v.artists.length; i++) {
                                artNameData += v.artists[i].name + "/";
                            }
                            artNameData = artNameData.slice(0, -1);
                            v.artNameData = artNameData;
                        });

                        this.searchData = data;
                        let ids = [];
                        data.map(v => {
                            ids.push(v.id);
                        });
                        ids = ids.join(",");
                        // 获取歌曲详情
                        this.getsongDatabyId(ids);
                    }
                })
                .catch(err => {});
        },

        //获取歌单歌曲详情
        getsongDatabyId(ids) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/song/detail",
                params: {
                    ids
                }
            })
                .then(result => {
                    this.$toast.clear();
                    this.songData = [];
                    result.data.songs.map(v => {
                        this.songData.push(v);
                    });

                    // 获取歌单的播放url
                    this.songData.map((v, index) => {
                        //设置歌手名称
                        let songar = "";
                        v.ar.map(arv => {
                            songar += arv.name + "/";
                        });
                        v.songAr = songar.slice(0, -1);
                        //设置播放音乐链接
                        v.palyurl =
                            "https://music.163.com/song/media/outer/url?id=" +
                            v.id +
                            ".mp3";
                        //设置active
                        v.isActive = false;
                        //获取歌手专辑（获取歌手图片）
                        setMusicData.getSingerAlbum(
                            this.songData,
                            index,
                            v.ar[0].id
                        );

                        //设置总时间
                        let m = Math.floor((v.dt / 1000 / 60) % 60);
                        let s = Math.floor((v.dt / 1000) % 60);
                        v.totalTime = m + "." + s;

                        //设置歌词
                        //获取歌词
                        setMusicData.getSongWordData(
                            this.songData,
                            index,
                            v.id
                        );
                    });
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌手专辑
        // getSingerAlbum(index, id) {
        //   this.$toast.loading({
        //     message: "加载中...",
        //     forbidClick: true,
        //     duration: 0,
        //     loadingType: "spinner",
        //   });
        //   this.axios({
        //     method: "get",
        //     url: "/artist/album",
        //     params: {
        //       id,
        //     },
        //   })
        //     .then((result) => {
        //       this.$toast.clear();
        //       this.songData[index].singerImg = result.data.artist.picUrl;
        //     })
        //     .catch((err) => {
        //       this.$toast.clear();
        //     });
        // },

        //点击设置音乐播放
        clicksetUrl(index) {
            this.show = false;
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            //传值到state
            this.setsongData(this.songData[index]);
            this.setClickSong(true);
            this.seticonNum(1);
            //跳转
        }
    }
};
</script>

<style lang="less" scoped>
</style>